.headerTitle {
  border-radius: 2px;
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: space-between;
  position: relative;
  height: 5rem;
  margin-bottom: 1rem;
}

@media screen and (max-width: 768px) {
  .headerTitle {
    height: 3rem;
    margin-bottom: 0;
  }
}
