.sectionBody {
  position: relative;
  width: 100%;
}

.slideIn {
  animation-name: slidein;
  animation-duration: 1.5s;
  min-width: 100%;

  /* overflow-x: scroll; */
}

@keyframes slidein {
  from {
    margin-top: 1rem;
    opacity: 0;
  }

  to {
    margin-top: 0rem;
    opacity: 1;
  }
}
